<template>
    <div id="container" v-if="NuiOpen" :style="position_nui">
        <div class="box">
            <div id="logo_loja"><img src="img/logo.png" /></div>
            
            <div id="informacao_loja">
                <p id="titulo_principal">Tatuagens</p>
                <p id="instrucao">Utilize as setas do teclado para selecionar as tatuagens</p>
                <p id="instrucao">Para comprar uma tatuagem,
                <span style="color: rgba(0, 255, 0, 0.75); text-shadow: 1px 2px black">PRESSIONE ENTER</span>
                </p>
                <p id="instrucao">
                    Para remover uma tatuagem,
                    <span style="color: rgba(255, 0, 0); text-shadow: 1px 2px black">PRESSIONE DELETE</span>
                </p>
            </div>
            
            <div id="RemoverTodasTatuagens" v-bind:class="{ hover2: selecionado == -1 }" v-if="selecionado==-1">{{text_button_remove_tattoos}}</div>

            <div id="tatuagens_lista" v-if="selecionado != -1">
                <ul v-for="(item, index) in list" v-bind:key="index">
                    <li :class="item.titulo + item.dlc">
                        <div id="local_range" v-bind:class="{ hover: selecionado == index }">
                            <img src="img/machine.png" />
                            <span id="info_tatto">{{ item.titulo }}:<span id="mpheist3_overlays_info">{{ item.atual }}</span></span>
                            <span class="dlc_valor" id="dlc_valor" style="margin-left: 0.5vh; font-size: 1.5vh; color: green">${{ item.valor }}</span>
                            <span class="dlc_total" id="dlc_total" style="margin-left: 0.5vh; font-size: 1.5vh; color: chocolate">Qtd: {{ item.qtd }}</span>
                            <span class="dlc_suas_total" id="dlc_suas_total" style="margin-left: 0.5vh; font-size: 1.5vh; color: orange" v-if="item.suas == 1">Tatuagem Adquirida!!!</span>
                            <span class="dlc_suas_total" id="dlc_suas_total" style="margin-left: 0.5vh; font-size: 1.5vh; color: red" v-else-if="item.suas == 2">Tatuagem Removida!!!</span>
                            <span class="dlc_possui" id="dlc_possui" style="margin-left: 0.5vh; font-size: 1.5vh; color: red" v-if="item.on">Já possui.</span>
                            <input type="range" min="1" :max="item.qtd" value="0" class="slider" id="mpheist3_overlays" v-model="item.atual"/>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Tattoo_Shop_And_Degrades",
    data() {
        return {
            NuiOpen: false,
            NameResource: 'HVCTattoo',
            position_nui: 'transform: scale(1.0);',
            selecionado: 0,
            list: [],

            text_button_remove_tattoos: "Remover todas as tatuagens"
        };
    },

    methods: {
        receiveLua: function (event) {
            if (!event || !event.data) return;

            const lua = event.data;

            if(lua.ui == "Keys") {
                this.TeclaPressionada(lua.mover);
            }
            else if(lua.ui == "Interface") {
                this.NuiOpen = lua.NuiOpen;

                if (lua.NuiOpen) {
                    this.list = lua.list;
                    this.position_nui = 'transform: scale('+lua.position_nui+');';
                }  
            } 
            
            if(lua.ui == "Info") {
                for (var i = 0; i < this.list.length; i++) {
                    this.list[i].on = false;
                    if (this.list[i].dlc == lua.dlc) {
                        this.list[i].on = lua.possui;
                    }
                }
            } else if(lua.ui == "Added") {
                for (var j = 0; j < this.list.length; j++) {
                    this.list[j].suas = 0;
                    if (this.list[j].dlc == lua.dlc) {
                        this.list[j].suas = lua.add;
                    }
                }
            }
        },

        // Ao utilizar o teclado para selecionar no menu, o scroll segue a seleção.
        scrollToClass(className) {
            let el = this.$el.getElementsByClassName(className)[0];
            el.scrollIntoView();
        },
    
        // Teclado do menu
        TeclaPressionada(tecla) {
            var keypress = false;
            console.log("PRESS KEY")
            if (tecla == "Cima") {
                if (this.selecionado < 0) {
                    this.selecionado = this.list.length - 1;
                } 
                else 
                    this.selecionado--;
            } 
            else if (tecla == "Baixo") {
                if (this.selecionado >= this.list.length - 1) {
                    this.selecionado = -1;
                } 
                else 
                    this.selecionado++;
            } 
            else if (tecla == "Direita" && this.selecionado >= 0) {
                keypress = true;
                if (this.list[this.selecionado].atual >= this.list[this.selecionado].qtd) {
                    this.list[this.selecionado].atual = 1;
                } 
                else 
                    this.list[this.selecionado].atual++;
            } 
            else if (tecla == "Esquerda" && this.selecionado >= 0) {
                keypress = true;
                if (this.list[this.selecionado].atual <= 1) {
                    this.list[this.selecionado].atual = this.list[this.selecionado].qtd;
                } 
                else 
                    this.list[this.selecionado].atual--;
            } else if(tecla == "Enter" || tecla == "Delete") 
                keypress = true;

            if (keypress) {
                if (this.selecionado >= 0) {
                    this.scrollToClass(this.list[this.selecionado].titulo + this.list[this.selecionado].dlc);

                    fetch(`https://${this.NameResource}/UpdateTattoo`, {
                        method: 'POST',
                        body: JSON.stringify({
                            mx: {tecla: tecla, dlc: this.list[this.selecionado].dlc, atual: this.list[this.selecionado].atual},
                        }),
                    });
                } else {
                    fetch(`https://${this.NameResource}/UpdateTattoo`, {
                        method: 'POST',
                        body: JSON.stringify({
                            mx: {tecla: tecla, dlc: 'RemoverAll', atual: 'RemoverAll'},
                        }),
                    });
                    this.text_button_remove_tattoos = "As tatuagens foram removidas";
                    setTimeout(function() {   
                        this.text_button_remove_tattoos = "Remover todas as tatuagens";
                    }.bind(this), 2500)
                }
            }
        },
    },

    created: function () {
        window.addEventListener("message", this.receiveLua);
    },

    destroyed: function () {
        window.removeEventListener("message", this.receiveLua);
    },
};
</script>

<style>
@import url("https://fonts.googleapis.com/css?family=Bebas+Neue");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}

body {
    user-select: none;
}

#container {
    float: right;
    color: white;
    font-family: "Bebas Neue";
    margin-right: 1vh;
    transform: scale(1.0);
}

.box {
    width: 50vh;
    height: 94vh;
    background-color: rgb(0, 0, 0, 0.5);
    border-radius: 1.5vh;
}

.box #logo_loja img {
    width: 50vh;
    height: 35vh;
    opacity: 0.75;
    border-top-left-radius: 1.5vh;
    border-top-right-radius: 1.5vh;
}

.box #titulo_principal {
    font-size: 3vh;
    background-color: rgb(167, 167, 167, 0.15);
    text-align: center;
    margin-top: -0.75vh;

    border-bottom-left-radius: 4vh;
    border-bottom-right-radius: 4vh;

    padding-top: 1vh;
    padding-bottom: 1vh;

    border-bottom: 0.2vh solid rgba(167, 167, 167, 0.5);
}

.box #instrucao {
    font-size: 2vh;
    text-align: center;
    margin-top: 1vh;
    padding-top: 1vh;
    padding-bottom: 1vh;
}

.box #tatuagens_lista {
    position: absolute;
    width: 50vh;
    height: 36vh;
    border-bottom-left-radius: 1.5vh;
    border-bottom-right-radius: 1.5vh;
    border-top-left-radius: 4vh;
    border-top-right-radius: 4vh;
    background-color: rgb(0, 0, 0, 0.15);

    overflow-y: scroll;
    overflow-x: hidden;

    border-top: 0.2vh solid rgba(167, 167, 167, 0.5);

    animation: move-open2;
    animation-duration: 0.2s;
}

.box #tatuagens_lista::-webkit-scrollbar {
    width: 0vh;
} /* configurando scroll */

#local_range {
    padding: 2vh 2vh;
}

#local_range li {
    position: absolute;
    font-size: 3vh;
}

#local_range img {
    position: absolute;
    widows: 6vh;
    height: 6vh;
}

#local_range:hover {
    background-color: rgb(167, 167, 167, 0.15);
    border-radius: 2vh;
    padding: 2vh 2vh;
}

.hover {
    background-color: rgb(167, 167, 167, 0.15);
    border-radius: 2vh;
    padding: 2vh 2vh;
}

#RemoverTodasTatuagens {
    font-size: 2vh;
    text-align: center;
    width: 30vh;

    margin-top: 6vh;
    margin-left: 10vh;

    animation: move-open2;
    animation-duration: 0.2s;

    border: 0.2vh solid rgba(255, 0, 0, 0.3);
}
.hover2 {
    background-color: rgb(167, 167, 167, 0.25);
    border-radius: 2vh;
    padding: 0.5vh 0.5vh;
    color: red;
}

#info_tatto {
    margin-left: 4.5vh;
    font-size: 2vh;
}

.dlc_total,
.dlc_valor,
.dlc_suas_total,
.dlc_possui {
    background-color: rgb(0, 0, 0, 0.25);
    border-radius: 5vh;
    padding: 0.5vh 0.5vh;

    animation: move-open2;
    animation-duration: 0.2s;
}

@keyframes move-open2{
    0%{
        /* top: 100%; */
        opacity: 0.0;
    }

    100%{
        /* top: 0%; */
        opacity: 1;
    }
}

.slider {
    -webkit-appearance: none;
    width: 35vh;
    height: 2vh;
    outline: none;
    background-color: rgb(28, 31, 32);
    background-size: 10vh 10vh;
    border-radius: 0.3vh;
    margin-left: 6.25vh;
}

.slider:hover {
    opacity: 1;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 1vh;
    height: 2vh;
    cursor: pointer;
    background-color: rgb(57, 139, 80, 0.75);
}

.slider::-moz-range-thumb {
    width: 30.75px;
    height: 45.75px;
    cursor: pointer;
}

#setasSimulacao {
    position: absolute;
    margin-top: 50vh;
    transform: scale(0.75);
    margin-left: -5vh;
}

#SetaEsquerda,
#SetaCima,
#SetaBaixo,
#SetaDireita {
    float: left;
    padding: 1vh 1vh;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
}

#SetaEsquerda:hover,
#SetaCima:hover,
#SetaBaixo:hover,
#SetaDireita:hover {
    background-color: rgba(0, 0, 0, 0.8);
}
</style>
